
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    flex-direction: column;
    background: rgb(52,44,193);
background: -moz-linear-gradient(0deg, rgba(52,44,193,1) 0%, rgba(255,222,233,1) 90%);
background: -webkit-linear-gradient(0deg, rgba(52,44,193,1) 0%, rgba(255,222,233,1) 90%);
background: linear-gradient(0deg, rgba(52,44,193,1) 0%, rgba(255,222,233,1) 90%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#342cc1",endColorstr="#ffdee9",GradientType=1);

}

.form-container form{
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    padding: 20px;
    background-color: #fff;
} 

.form-container form .title{
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display", serif;
}

.form-container form input{
    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
}

.form-container form button{
    border: 1px solid black;
    border-radius: 0;
    background-color: #000;
    color: white;
    width: 250px;
}

.form-container form button:hover{
    border: 1px solid black;
    background: #000000;
    background: -webkit-linear-gradient(to right,#434343,#000000);
    background: linear-gradient(to right,#434343,#000000);
    
}