@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
/* font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif; */
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}  

/* navbar css */
.navbar{
  font-family:  'Poppins', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.1px solid gray !important;
}

.nav-link{
  font-weight: 300 !important;
}

.active{
  border-bottom: 2px solid black;
}

.navbar-brand{
  font-weight: 700;
  font-family:  'roboto', sans-serif;
  letter-spacing: 1px;
}

/* ======================
=========footer======= */

.footer{
  color: white;
  padding: 25px;
  background: rgb(2,0,36);
  background: -moz-linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(66,66,74,0.9977240896358543) 45%);
  background: -webkit-linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(66,66,74,0.9977240896358543) 45%);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(66,66,74,0.9977240896358543) 45%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#42424a",GradientType=1);
}

.footer a{
  text-decoration: none;
  color: white;
  padding: 10px;
}
.footer a:hover{
  color: #646464;
  border-bottom: 1px solid #646464 ;
}

/* =====================
  ==PageNotFound========== */
  .pnf{
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .pnf-title{
    font-size: 100px;
    font-weight: 700;
  }

  .pnf-heading{
    font-weight: normal;
  }

  .pnf-btn{
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;

  }
  .pnf-btn:hover{
       background-color: black;
       color: white;
  }



  /* ======================
  =====contactus========== */
  .contactus {
    margin: 0;
    padding: 0;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /* =========== */
  .product-link{
    text-decoration: none !important;
    color: black !important;
  }

  



  












